/**=====================
    77. avatars CSS start
==========================**/
.avatars {
  .avatar {
    display: inline-block;
    margin-right: 10px;
    position: relative;
    width: auto;
    &:last-child {
      margin-right: 0;
    }
    .b-r-30 {
      border-radius: 30px;
    }
    .b-r-35 {
      border-radius: 35px;
    }
    .b-r-25 {
      border-radius: 25px;
    }
    .b-r-15 {
      border-radius: 15px;
    }
    &.ratio {
      img {
        width: auto;
      }
    }
    .status {
      position: absolute;
      background-color: $light-text;
      bottom: 0;
      right: 4px;
      border-radius: 100%;
      border: 1px solid $white;
    }
    .status {
      position: absolute;
      height: 12%;
      width: 12%;
      border-radius: 100%;
      bottom: 12%;
      right: 4%;
    }
  }
}
.customers {
  ul {
    display: inline-block;
    li {
      img {
        border: 2px solid $white;
        box-shadow: -2px 2px 4px 0px rgba(0, 0, 0, 0.2);
        transition: 0.5s;
        &:hover {
          transform: translateY(-4px) scale(1.02);
          transition: 0.5s;
        }
      }
      + li {
        margin-left: -10%;
      }
      &:nth-child(4) {
        margin-left: -4px;
      }
    }
  }
  &.avatar-group {
    margin-right: 30px;
    &:last-child {
      margin-right: 0;
    }
  }
}
.avatar-showcase {
  margin-bottom: -10px;
  .avatars {
    .avatar {
      margin-bottom: 10px;
    }
  }
  .avatar-group {
    margin-bottom: 10px;
  }
  .ratio {
    > * {
      position: relative;
      top: unset;
      left: unset;
    }
  }
  .add-friend {
    .d-block {
      margin: 8px 0;
    }
  }
}
.tagpills-badge{
  .basic-icontag{
    margin-bottom: -5px;
    a.badge{
      padding: 6px 8px;
      margin-bottom: 5px;
    }
    .badge{
      svg{
        width: 18px;
        height: 18px;
        stroke-width: 2px;
      }
    }
  }
  .context{
    .badge{
      padding: 5px 5px 4px;
    }
  }
  .digits{
    .badge{
      padding: 4px 6px 2px;
    }
  }
  .icon{
    .badge{
      padding: 3px 5px 3px;
    }
  }
  .height-equal{
    .btn{
      .badge{
        top: 0;
      }
    }
  }
}
/**=====================
    77. avatars CSS Ends
==========================**/