%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}
%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}
%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}
.chart-block {
  canvas {
    width: 100% !important;
  }
  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}
.flot-chart-container {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  height: 400px;
  #toggling-series-flot {
    width: 79% !important;
  }
}
.all-chart {
  .flot-chart-container {
    height: 350px;
  }
}
.flot-chart-placeholder {
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 1.2em;
  text-align: center;
  .legend table {
    border-spacing: 5px;
  }
}
.morris-hover {
  position: absolute;
  z-index: 8;
  &.morris-default-style {
    border-radius: 10px;
    padding: 6px;
    color: $dark-gray;
    background: $white;
    border: solid 1px $light-gray;
    font-family: $font-serif;
    font-size: 12px;
    text-align: center;
    .morris-hover-row-label {
      font-weight: bold;
      margin: 0.25em 0;
    }
    .morris-hover-point {
      white-space: nowrap;
      margin: 0.1em 0;
    }
  }
}
.chart-sparkline {
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
  background: rgba(49, 49, 49, 0.7);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
  color: $white;
  font: 10px $font-Montserrat;
  text-align: left;
  white-space: nowrap;
  padding: 5px;
  border: 1px solid rgba(49, 49, 49, 0.75);
  box-sizing: content-box;
  z-index: 10000;
  border-radius: 5px;
  .jqsfield {
    color: $white;
    font: 10px $font-Montserrat;
    text-align: left;
  }
}
.pieLabel {
  div {
    color: $white !important;
    input[type="checkbox"] {
      margin: 7px;
    }
  }
}
.card-body.chart-block.chart-vertical-center{
  canvas{
    height: 355px !important;
  }
}
.card-body.chart-block{
  .chart-overflow{
    border-radius: 0 0 20px 20px;
  }
}
// knob chart css
.knob-chart {
  min-height: 255px;
  .chart-clock-main {
    position: relative;
    width: 200px;
    margin: 45px auto;
    .clock-large {
      position: absolute;
      left: -48px;
      top: -45px;
    }
    .clock-medium {
      position: absolute;
      left: 2px;
      top: 5px;
    }
    .clock-small {
      position: absolute;
      left: 6px;
      top: 55px;
    }
  }
}
// peity chart css
.peity-charts {
  svg {
    width: 100%;
    height: 250px;
  }
  &.peity-chart-primary {
    svg {
      polygon {
        fill: rgba($primary-color, 0.2);
      }
      polyline {
        stroke: var(--theme-deafult);
      }
    }
  }
}
.bar-odd-even {
  svg {
    rect {
      &:nth-child(odd) {
        fill: var(--theme-deafult);
      }
      &:nth-child(even) {
        fill: var(--theme-secondary);
      }
    }
  }
}
.donut-peity {
  svg {
    path {
      &:nth-child(odd) {
        fill: var(--theme-deafult);
      }
      &:nth-child(even) {
        fill: var(--theme-secondary);
      }
    }
  }
}
.small-donut,
.donut-peity2,
.pie-peity {
  ~ svg,
  svg {
    path {
      &:nth-child(odd) {
        fill: var(--theme-deafult);
      }
      &:nth-child(even) {
        fill: $light-semi-gray;
      }
    }
  }
}
.small-donut2 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: var(--theme-secondary);
      }
    }
  }
}
.small-donut3 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $success-color;
      }
    }
  }
}
.small-donut4 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $info-color;
      }
    }
  }
}
.small-donut5 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $warning-color;
      }
    }
  }
}
.small-donut6 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $danger-color;
      }
    }
  }
}
p {
  &.data-attributes {
    svg {
      margin-right: 10px;
    }
  }
}
// knob chart
.knob-block {
  position: relative;
  input {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }
}
.knob-input-disable {
  input {
    display: none !important;
  }
}
// apex chart
.apex-chart {
  margin: 0 auto;
}
.apexcharts-toolbar {
  z-index: 1 !important;
}
.apexcharts-menu-item {
  width: 110px;
}
.word-tree {
  height: 400px;
}
// chartist chat
.ct-series-a {
  .ct-slice-pie,
  .ct-slice-donut-solid,
  .ct-area {
    fill: var(--theme-deafult);
  }
  .ct-point,
  .ct-line,
  .ct-bar,
  .ct-slice-donut {
    stroke: var(--theme-deafult);
  }
}
.ct-series-b {
  .ct-point,
  .ct-line,
  .ct-bar,
  .ct-slice-donut {
    stroke: var(--theme-secondary);
  }
  .ct-slice-pie,
  .ct-slice-donut-solid,
  .ct-area {
    fill: var(--theme-secondary);
  }
}
.test-buttons {
  button {
    + button {
      margin-left: 10px;
    }
  }
}
/**=====================
     41. Chart CSS End
==========================**/