/**=============================
	55. theme layout CSS Start
================================**/

$dark-background: #383838;
$dark-card-inbox: #323846;
$dark-small-font-color: #98a6ad;
$dark-all-font-color: rgba(255, 255, 255, 0.8);
$light-all-font-color: rgba(255, 255, 255, 0.2);
$sidebar-submenu-font-color:rgba(255, 255, 255, 0.6);

body {
  &.dark-only {
    color: $dark-all-font-color;
    background-color: $dark-body-background;
    .employee-status .d-flex .flex-grow-1 h5 a{
      color: $dark-all-font-color;
    }
    .user-status{
      table{
        tr{
          th,td{
            &:first-child{
              a{
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
    }
    &.box-layout{
      .page-wrapper{
        .page-header{
          background-color: $dark-card-background;
        }
      } 
    }
    .fc-theme-standard .fc-scrollgrid{
      border: 1px solid $dark-card-border;
    }
    .custom-profile .profile-details h4 a {
      color: $dark-all-font-color;
    }
    .apexcharts-tooltip.light {
      .apexcharts-tooltip-title {
        background: $dark-card-background;
        color: $white;
      }
    }
    hr {
      background-color: $dark-card-background;
    }
    .progress {
      background-color: $dark-card-border;
    }
    .card {
      background-color: $dark-card-background;
    }
    .ecommerce-widget {
      border: 1px solid $dark-card-border !important;
    }
    .btn-light {
      color: $dark-all-font-color !important;
    }
    .b-r-light {
      border-color: $dark-card-border !important;
    }
    .form-select {
      color: rgba(255,255,255,0.5);
    }
    .add-project {
      .text-inherit {
        color: $dark-all-font-color;
      }
    }
    .todo-wrap{
      .card-header{
        &.b-bottom{
          border-bottom: 1px solid $dark-card-border;
        }
      }
    }
    .page-wrapper{
      .page-body-wrapper{
        .page-body{
          .card:not(.email-body){
            .bottom-content{
              .b-r-light{
                border-left: unset !important;
              }
            }
          }
        }
      }
    }
    .basic-dropdown{
      ul{
        li{
          span.f-w-600{
            color: $dark-all-font-color;
            &::before{
              background-color: rgba($white, 0.1);
            }
          }
        }
      }
      .left-dropdown{
        background-color: $dark-body-background;
        a{
          color: $dark-all-font-color;
        }
      }
    }
    .knowledgebase-search {
      color: $theme-body-font-color;
      .form-control-plaintext {
        color: $theme-body-font-color;
      }
    }
    .btn-close {
      filter: brightness(0.8) invert(1);
    }
    .prooduct-details-box {
      .d-flex {
        border: 1px solid $dark-card-border;
      }
    }
    .pricingtable {
      box-shadow: 0 0 40px rgba($dark-card-border, 0.5);
    }
    .product-price {
      del {
        color: $dark-small-font-color;
      }
    }
    .ProfileCard {
      border: 1px solid $dark-card-border !important;
    }
    .form-control-plaintext {
      color: $dark-all-font-color;
    }
    .form-select {
      background-color: $dark-body-background;
      border: 1px solid $dark-card-border;
    }
    .social-status{
      .d-flex{
        .flex-grow-1{
          a{
            color: $dark-all-font-color;
          }
        }
      }
    } 
    .left-header{
      .left-menu-header{
        ul.header-left{
          li{
            span.f-w-600{
              &::before{
                z-index: 0;
                background-color: rgba($white , 0.1);
              }
            }
          }
        }
      }
    }
    .box-layout {
      background-color: $dark-body-background;
      .page-wrapper,
      &.page-wrapper {
        .page-header {
          .header-wrapper {
            border: 1px solid $dark-border-color;
          }
        }
      }
    }
    .form-check-input {
      background-color: $dark-body-background;
    }
    .switch-state {
      &:before {
        background-color: $dark-background;
      }
    }
    .icon-state {
      .switch-state {
        &:after {
          color: $dark-all-font-color;
        }
      }
    }
    .page-wrapper {
      .page-body-wrapper {
        .page-body{
          .card:not(.email-body) {
            .table {
              &.table-light{
                tr{
                  th,td{
                    color: $theme-body-font-color;
                  }
                }
              }
            } 
          }
        }   
      }  
    }
    .chat-box{
      .chat-menu{
        .user-profile{
          .user-content{
            h5{
              a{
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
      .about{
        .name{
          a{
            color: $dark-all-font-color;
          }
        }
      }
    }
    .file-manager {
      > h6 {
        color: $dark-all-font-color;
      }
    }
    .note-editor{
      i{
        color: $dark-all-font-color;
      }
      span{
        color: $dark-all-font-color;
      }
      i.note-recent-color{
        color: $theme-body-font-color;
      }
    }
    .form-switch {
      .form-check-input {
        background-color: $dark-background;
        &:checked {
          background-color: $primary-color;
        }
      }
    }
    .page-wrapper{
      &.horizontal-wrapper{
        .page-body-wrapper{
          .sidebar-wrapper{
            .sidebar-main{
              .sidebar-links{
                .simplebar-wrapper{
                  .simplebar-mask{
                    .simplebar-content-wrapper{
                      .simplebar-content{
                        > li{
                          &.hoverd{
                            .sidebar-submenu{
                              li{
                                a{
                                  color: $dark-all-font-color;
                                  &.active{
                                    color: $primary-color;
                                  }
                                  &:after{
                                    background-color: $dark-all-font-color;
                                  }
                                }
                              }
                            }
                          }
                          > a{
                            color: $dark-all-font-color;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
      .page-header{
        .header-wrapper{
          .nav-right{
            .message-dropdown{
              li{
                .d-flex{
                  .flex-grow-1{
                    h5{
                      a{
                        color: $dark-all-font-color;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .project-list {
      .border-tab {
        &.nav-tabs {
          .nav-item {
            .nav-link {
              border-bottom: $dark-card-border;
            }
          }
        }
      }
    }
    .main{
      .langChoice{
        border-color: $dark-card-border;
        &:focus, &:focus-visible{
          outline-color: $dark-card-border;
        }
      }
    } 
    .chart-block{
      .flot-chart-container{
        .legend{
          table{
            color: $dark-small-font-color !important;
            background-color: $dark-body-background;
          }
        }
      }
    }
    .add-post{
      .dropzone{
        .dz-preview{
          .dz-error-message{
            background-color: $dark-body-background !important;
          }
        }
      }
    }   
    .comment-box{
      .comment-social{
        li{
          &:nth-child(n+2){
            border-left: 1px solid $dark-card-border;
          }
        }
      }
      .d-md-flex{
        h6{
          color: $dark-all-font-color;
        }
        img{
          border: 1px solid $dark-card-border;
        }
      }
    }
    .chart-block{
      .chart-overflow{
        svg{
          line{
            stroke: $dark-body-background;
          }
          g{
            text{
              fill: $dark-all-font-color;
            }
          }
        }
      }
    }
    .apexcharts-yaxistooltip{
      color: $dark-all-font-color;
      background-color: $dark-body-background;
    }
    .apexcharts-xaxistooltip {
      color: $dark-all-font-color;
      background-color: $dark-body-background;
    }
    .apexcharts-yaxis-title {
      color: $dark-all-font-color;
    }
    .apexcharts-xaxistooltip-bottom {
      &:after {
        border-bottom-color: $primary-color;
      }
    }
    .onhover-show-div {
      background-color: $transparent-color;
    }
    .product-box {
      background-color: $dark-card-background;
      .product-details {
        h4 {
          color: $dark-all-font-color;
        }
      }
      .modal {
        .modal-header {
          background-color: $dark-card-background;
        }
      }
    }
    .product-wrapper {
      &.sidebaron {
        .product-sidebar {
          .filter-section {
            .card {
              .left-filter {
                border-color: $dark-card-border;
              }
            }
          }
        }
      }
    }
    .product-page-main {
      p {
        color: $dark-small-font-color;
      }
      .pro-group {
        &:nth-child(n + 2) {
          border-color: $dark-card-border;
        }
      }
      .border-tab {
        &.nav-tabs {
          .nav-item {
            .nav-link {
              &.active {
                color: $white !important;
              }
            }
          }
        }
      }
    }
    .color-selector {
      ul {
        li {
          border-color: $dark-card-border;
        }
      }
    }
    .bookmark-tabcontent {
      .list-bookmark.details-bookmark {
        .bookmark-card.card {
          .title-bookmark {
            border-top: unset;
          }
        }
      }
      .details-bookmark {
        .bookmark-card.card {
          .title-bookmark {
            border-top: 1px solid $dark-card-border;
          }
        }
      }
    }
    .product-social {
      li {
        a {
          border-color: $dark-card-border;
        }
      }
    }
    .invoice {
      .invo-header {
        border-color: $dark-card-border;
      }
    }
    .prooduct-details-box {
      .product-name {
        h6 {
          color: $dark-all-font-color;
        }
      }
    }
    .checkout {
      label {
        color: $dark-all-font-color;
      }
    }
    .page-wrapper{
      .page-header{
        .header-wrapper{
          .nav-right{
            .onhover-show-div:not(.profile-dropdown){
              li{
                &:first-child{
                  background-color: rgba($primary-color, 0.1) !important;
                }
              }
            }
          }
        }
      }
    }
    .setting-list{
      background-color: $dark-card-background;
      .setting-option{
        li{
          .setting-light{
            background-color: $dark-body-background;
            i{
              color: $dark-all-font-color;
            }
            &:hover{
              background-color: rgba($primary-color, 0.1);
              i{
                color: $primary-color;
              }
            }
          }
        }
      }
    }
    .todo-wrap{
      .todo-list-body{
        .assign-name{
          color: $dark-small-font-color;
        }
      }
      .email-wrap{
        .email-app-sidebar{
          .main-menu{
            > li{
              a{
                &:hover{
                  background-color: transparent;
                  color: $primary-color;
                  .badge{
                    color: $white;
                  }
                }
              }
            }
          }
        }
      }
    }
    .todo {
      .todo-list-wrapper {
        #todo-list {
          li {
            &:last-child{
              .task-container{
                border-bottom: 0;
              }
            }
            .task-container {
              background: $dark-card-background;
              border-bottom: 1px solid $dark-card-border;
              .task-label {
                color: $dark-all-font-color;
              }
              &:hover {
                h4 {
                  color: $white;
                }
              }
              .task-action-btn {
                .action-box {
                  &:hover:not(.active) {
                    background: $dark-card-background;
                    border: 1px solid $dark-border-color;
                  }
                }
              }
            }
            &.completed {
              .task-container {
                .task-label {
                  color: $primary-color;
                }
                .complete-btn {
                  &:hover {
                    border: 1px solid $success-color !important;
                  }
                }
              }
            }
          }
        }
        .todo-list-header {
          .new-task-wrapper {
            textarea {
              background-color: $dark-card-background;
              border: 1px solid $dark-card-border;
              color: $dark-all-font-color;
            }
          }
        }
      }
    }
    .border-bottom{
      border-bottom: 1px solid $dark-card-border !important;
    }
    .email-wrap {
      .email-right-aside {
        .email-body {
          .inbox {
            .d-flex {
              &:hover {
                background-color: rgba($primary-color, 0.1);
              }
            }
          }
        }
      }
      .email-wrapper {
        .emailread-group {
          &:nth-child(n + 2) {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .cke_chrome {
      border-color: $dark-card-border;
    }
    .card {
      .icon-box {
        background-color: $dark-body-background;
        &:hover {
          background-color: rgba($primary-color, 0.1);
        }
      }
    }
    .apexcharts-tooltip.light {
      .apexcharts-tooltip-title {
        background: $dark-card-background;
        color: $white;
      }
    }
    .apexcharts-tooltip.apexcharts-theme-light {
      border: 1px solid $dark-card-border;
      background: $dark-body-background;
    }
    .apexcharts-tooltip.apexcharts-theme-light .apexcharts-tooltip-title {
      border: 1px solid $dark-card-border;
      background: $dark-body-background;
      color: $dark-all-font-color;
    }
    .apexcharts-tooltip-series-group {
      color: $dark-all-font-color !important;
    }
    .apexcharts-yaxistooltip,
    .apexcharts-xaxistooltip {
      color: $dark-all-font-color;
      background: $dark-body-background;
      border: 1px solid $dark-card-border;
    }
    .apexcharts-yaxistooltip-left {
      &::before,
      &::after {
        border-left-color: $dark-card-border;
      }
    }
    .apexcharts-xaxistooltip-bottom {
      &::before,
      &::after {
        border-bottom-color: $dark-card-border;
      }
    }
    .bottom-content{
      .apexcharts-canvas {
        span{
          color: $dark-all-font-color;
        }
      }
    }
    .apexcharts-canvas {
      .apexcharts-tooltip {
        border: 1px solid $dark-card-border;
        .apexcharts-tooltip-title {
          margin-bottom: 0;
        }
        .apexcharts-tooltip-series-group {
          background-color: $dark-card-background;
          color: $dark-all-font-color;
        }
      }
      .apexcharts-yaxis {
        text {
          fill: $dark-small-font-color;
        }
      }
    }
    .apexcharts-xaxistooltip{
      background-color: $dark-body-background;
      color: $dark-all-font-color;
      border: 1px solid $dark-card-border;
    }
    .apexcharts-xaxistooltip-bottom:before {
      border-bottom-color: $dark-card-border;
    }
    .apexcharts-xaxistooltip-bottom:after {
      border-bottom-color: $dark-card-border;
    }
    .apexcharts-tooltip.apexcharts-theme-light {
      border: 1px solid $dark-card-border;
      background: $dark-body-background;
      .apexcharts-tooltip-text {
        color: $dark-small-font-color;
      }
      .apexcharts-tooltip-title {
        background: $dark-card-background;
        border-bottom: 1px solid $dark-card-border;
        color: $dark-small-font-color;
      }
    }
    .apexcharts-yaxis{
      text{
        color: $dark-all-font-color;
      }
    }
    .page-wrapper{
      .page-header{
        .header-wrapper{
          .nav-right{
            .notification-dropdown{
              li{
                + li{
                  p{
                    a{
                      color: $dark-all-font-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .product-box{
      .product-img{
        .product-hover{
          ul{
            li{
              &:hover{
                background-color: $primary-color;
              }
            }
          }
        }
      }
    }
    .categories{
      ul{
        li{
          &:hover{
            a{
              color: $primary-color;
            }
          }
        }
      }
    }
    // dashboard css
    .goal-view {
      ul {
        &.goal-list {
          border-color: $dark-card-border;
          li {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .dashboard-default{
      .appointment-detail{
        .card-body{
          .table{
            tr{
              td{
                .d-flex{
                  a{
                    span{
                      color: $dark-all-font-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .profile-greeting{
        .greeting-user{
          h4{
            a{
              color: $dark-all-font-color;
            }
          }
        }
      }
    }   h4 a
    //general-widget
    .default-datepicker {
      .datepicker-inline {
        .datepicker {
          background: $dark-card-background;
          .datepicker--nav-title {
            color: $dark-all-font-color;
            i {
              color: $dark-all-font-color;
            }
          }
          .datepicker--content {
            .datepicker--days {
              .datepicker--days-names,
              .datepicker--cells {
                .datepicker--day-name,
                .datepicker--cell-day {
                  color: $dark-all-font-color;

                  &.-selected- {
                    color: $white;
                  }
                }
              }
            }
          }
        }
      }
    }
    .static-widget {
      h6 {
        color: $dark-all-font-color;
      }
    }
    .widget-joins {
      .widget-card {
        border-color: $dark-card-border;
      }
    }
    .widget-feedback {
      ul {
        border-color: $dark-card-border;
        li {
          &:nth-child(n + 2) {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .customers {
      ul {
        li {
          img {
            border: 2px solid $dark-card-background;
          }
        }
      }
    }
    .jkanban-container {
      .card-header {
        p {
          color: $dark-small-font-color;
        }
      }
    }
    .product-page-main {
      .border-tab.nav-tabs {
        border-bottom: 0 !important;
      }
    }
    .custom-profile {
      .card-social {
        li {
          border-color: $dark-card-border;
        }
      }
      .card-footer {
        > div {
          [class*="col-"] {
            &:hover {
              h6 {
                color: $dark-all-font-color;
              }
            }
          }
          h6 {
            color: $dark-small-font-color;
          }
          div {
            + div {
              border-color: $dark-card-border;
            }
          }
        }
      }
    }
    .activity-media {
      .d-flex {
        &:nth-child(n + 2) {
          border-color: $dark-card-border;
        }
        .flex-grow-1 {
          span {
            color: $dark-small-font-color;
          }
        }
      }
    }
    .social-widget-card {
      h6 {
        color: $dark-all-font-color;
      }
    }
    // project create
    .project-cards {
      .project-list {
        .card {
          .border-tab.nav-tabs {
            border-bottom: unset !important;
          }
        }
      }
    }
    .tilt-showcase{
      .card-body{
        pre{
          background-color: $pre-tag-bg-color;
          code{
            background-color: $pre-tag-bg-color;
          }
        }
      }
    }
    .btn-light.disabled{
      color: $black !important;
    }
    // daterangepicker css start
    .daterangepicker {
      &:before {
        border-bottom-color: $dark-card-background;
      }
      .ranges {
        li {
          background-color: $dark-body-background;
          border-color: $dark-card-background;
          color: $dark-gray;
        }
      }
      .daterangepicker_input {
        .form-control {
          background-color: $dark-body-background;
          border-color: $dark-card-background !important;
        }
      }
      .calendar-time {
        select {
          border-color: $dark-card-background;
          background-color: $dark-body-background;
        }
      }
      &.dropdown-menu {
        background-color: $dark-card-background;
        box-shadow: 0 0 14px $dark-body-background;
      }
      .calendar-table {
        background-color: $dark-card-background;
        border-color: $dark-card-background;
      }
      &.ltr {
        td {
          &.in-range,
          &.off {
            background-color: $dark-body-background;
            color: $white;
          }
        }
      }
    }
    .daterangepicker {
      select.monthselect , select.yearselect {
        background-color: $dark-body-background;
        border-color: $dark-card-border;
      }
       
    }
    .datepicker--pointer {
      background-color: $dark-card-border;
      border-color: $dark-card-border;
    }
    .datepicker--nav {
      border-color: $dark-card-border;
    }
    .datepicker--cell-day {
      color: $dark-all-font-color;
    }
    .datepicker {
      background-color: $dark-card-background;
      .datepicker--cell{
        color: $white;
      }
    }
    .stepwizard {
      .stepwizard-row {
        .stepwizard-step {
          .btn-light {
            background-color: $dark-body-background !important;
            &.btn-primary {
              background-color: $primary-color !important;
            }
          }
        }
      }
    }
    // form wizard
    .f1 {
      .f1-steps {
        .f1-progress {
          background-color: $dark-card-border;
        }
        .f1-step {
          .f1-step-icon {
            background-color: $dark-card-border;
            color: $dark-all-font-color;
          }
          &.active {
            .f1-step-icon {
              background-color: $primary-color;
            }
          }
        }
      }
    }
    // clock picker
    .clockpicker-popover {
      .popover-title {
        background-color: $dark-card-background;
        border-color: $dark-card-border;
      }
    }
    .popover {
      &.bottom {
        > .arrow {
          &:after {
