            background-color: $btn-color !important;
            border-color: $btn-color !important;
            color: $white !important;
          }
        }
        input[type="checkbox"] {
          &:checked {
            & + label {
              &::before {
                background-color: $btn-color !important;
                border-color: $btn-color !important;
                color: $white !important;
              }
              &::after {
                color: $white !important;
              }
            }
          }
        }
      }
    }
    .bootstrap-datetimepicker-widget {
      ul {
        li {
          color: $dark-all-font-color;
          border-bottom: 1px solid $dark-border-color;
        }
      }
      tr {
        th,
        td {
          &:hover {
            background-color: $dark-card-background;
          }
          span {
            &:hover {
              background-color: $dark-card-background;
            }
          }
          &.prev,
          &.next {
            &:hover {
              background-color: $dark-card-background;
            }
          }
        }
        &:first-child {
          th {
            &:hover {
              background-color: $dark-card-background;
            }
          }
        }
      }
    }
    .btn-transparent {
      color: $dark-all-font-color;
    }
    #cal-basic {
      .fc-toolbar {
        .fc-left,
        .fc-right {
          .fc-next-button {
            .fc-icon-right-single-arrow {
              &:after {
                color: $dark-small-font-color;
              }
            }
          }
          .fc-prev-button {
            .fc-icon-left-single-arrow {
              &:after {
                color: $dark-small-font-color;
              }
            }
          }
        }
        .fc-left {
          .fc-button-group {
            .fc-month-button {
              color: $white !important;
            }
          }
        }
      }
    }
    .fc-button-group {
      .fc-basicWeek-button,
      .fc-basicDay-button,
      .fc-agendaWeek-button,
      .fc-agendaDay-button {
        color: $black !important;
      }
      .fc-month-button {
        color: $white !important;
      }
    }
    #cal-agenda-view {
      .fc-toolbar {
        .fc-right {
          .fc-button-group {
            .fc-month-button,
            .btn-light {
              color: $black !important;
            }
            .fc-agendaWeek-button {
              color: $white !important;
              &:hover {
                color: $black !important;
              }
            }
          }
        }
      }
    }
    .basic-calendar,
    #cal-bg-events,
    #cal-event-colors {
      .fc-toolbar {
        .fc-right {
          .fc-button-group {
            .fc-basicWeek-button,
            .btn-light {
              color: $black !important;
            }
            .fc-month-button {
              color: $white !important;
              &:hover {
                color: $black !important;
              }
            }
          }
        }
      }
    }
    .categories {
      ul {
        li {
          a {
            color: $dark-small-font-color;
          }
          &:hover {
            background-color: $dark-body-background;
          }
        }
      }
    }
    .socialprofile {
      .likes-profile {
        h5 {
          span {
            color: $dark-all-font-color;
          }
        }
      }
      span {
        color: $dark-small-font-color;
      }
    }
    .social-status {
      form{
        .form-group{
          .form-control-social{
            background-color: $dark-body-background;
            border: 1px solid $dark-card-border;
          }
        }
      }
      .d-flex {
        .flex-grow-1 {
          span {
            + span {
              color: $dark-small-font-color;
            }
          }
          p,
          .light-span {
            color: $dark-small-font-color;
          }
        }
      }
    }
    .filter-cards-view,
    .timeline-content {
      p {
        color: $dark-small-font-color;
      }
      .comment-number {
        i {
          color: $dark-small-font-color;
        }
      }
    }
    .search-page{
      ul.search-info{
        .rating{
          li{
            + li{
              border-left: none;
            }
          }
        }
      }
    }
    .social-tab{
      .input-group{
        .input-group-text{
          background-color: $primary-color;
        }
      }
    }  
    .social-chat {
      .flex-grow-1 {
        border: 1px solid $dark-card-border;
        &:after {
          border-right: 7px solid $dark-card-background;
        }
      }
      span {
        span {
          color: $dark-small-font-color;
        }
      }
    }
    .details-about {
      .your-details {
        p {
          color: $dark-small-font-color;
        }
      }
    }
    .activity-log {
      .my-activity {
        p {
          color: $dark-small-font-color;
        }
      }
    }
    .bookmark {
      ul {
        li {
          a {
            color: $dark-all-font-color;
          }
        }
      }
    }
    .bookmark-tabcontent {
      .tab-content {
        .card {
          .card-header {
            border-color: $dark-card-border;
          }
        }
      }
    }
    .list-persons {
      .profile-mail {
        .email-general {
          border-color: $dark-card-border;
        }
      }
    }
    .form-bookmark {
      .form-group {
        .select2-container {
          .select2-selection--single {
            border-color: $dark-card-border !important;
          }
        }
      }
    }
    .calendar-basic {
      #menu-navi {
        border-color: $dark-card-border;
      }
    }
    .wizard-4 {
      ul.anchor {
        background-color: $dark-card-background;
        li {
          h5 {
            color: $dark-all-font-color;
          }
          small {
            color: $dark-small-font-color;
          }
          .selected {
            h5 {
              color: $primary-color;
            }
            small {
              color: $primary-color;
            }
          }
        }
      }
      .step-container {
        background-color: $dark-body-background;
      }
    }
    .apexcharts-tooltip.light {
      border-color: $dark-card-border;
      background-color: $dark-body-background;
      .apexcharts-tooltip-title {
        border-color: $dark-card-border;
      }
      .apexcharts-tooltip-text-label,
      .apexcharts-tooltip-text-value {
        color: $white;
      }
    }
    @media screen and (max-width: 1660px) {
      .chat-box {
        .chat-history {
          .total-time {
            h2 {
              color: $dark-all-font-color;
            }
          }
        }
      }
      .caller-img {
        img {
          opacity: 0.7;
        }
      }
      .chat-box {
        .chat-history {
          .call-content {
            > div {
              background-color: $dark-card-background;
              background-blend-mode: overlay;
            }
          }
        }
      }
    }
    @media only screen and (max-width: 1199.98px) {
      .left-header {
        .mega-menu-container {
          border-left: 1px solid $dark-card-border;
          .mega-box {
            + .mega-box {
              border-left: none;
            }
          }
        }
      }
    }
    @media only screen and (max-width: 1199px) {
      .mobile-title {
        &.d-none {
          border-bottom: 1px solid $dark-card-border;
        }
      }
      .chat-menu {
        border-top: 1px solid $dark-border-color;
        background-color: $dark-card-background;
      }
      .error-wrapper {
        .maintenance-icons {
          li {
            i {
              color: $dark-color;
            }
          }
        }
      }
      .md-sidebar {
        .md-sidebar-aside {
          background-color: $dark-card-background;
          border-color: $dark-card-border;
        }
      }
    }
    @media only screen and (max-width: 575.98px) {
      .user-profile {
        .hovercard {
          .info {
            .user-designation {
              border-top: 1px solid $dark-border-color;
              border-bottom: 1px solid $dark-border-color;
            }
          }
        }
      }
      .page-header {
        .header-wrapper {
          .left-header {
            ul {
              li {
                .search-form {
                  .form-control-plaintext {
                    background-color: $dark-body-background;
                  }
                }
              }
            }
          }
        }
      }
      .page-wrapper {
        &.compact-wrapper{
          .page-header {
            .header-wrapper {
              .toggle-sidebar {
                border-right: 1px solid $dark-card-border;
              }
            }
          }
        }
      }
    }
    @media only screen and (max-width: 991.98px) {
      .page-wrapper {
        &.compact-wrapper {
          .page-header {
            .header-wrapper {
              .toggle-sidebar {
                svg {
                  stroke: $white;
                }
                &:hover {
                  background-color: $primary-color;
                }
              }
            }
          }
        }
      }
      .page-wrapper {
        &.compact-wrapper{
          .left-header {
            .mega-menu {
              svg {
                stroke: $dark-all-font-color;
              }
            }
          }
        }
      }
    }
    @media only screen and (max-width: 767.98px) {
      .contacts-tabs {
        .nav-pills {
          border-right: none;
          border-bottom: 1px solid $dark-card-border;
        }
      }
      .page-wrapper {
        &.compact-wrapper {
          .left-header {
            .mega-menu {
              svg {
                stroke: $white;
              }
              .nav-link {
                border-left: 1px solid $dark-card-border;
              }
            }
          }
        }
        &.material-icon {
          .page-header {
            background-color: $dark-card-background;
          }
        }
      }
      .left-header {
        .input-group {
          .form-control {
            background-color: $dark-card-background;
          }
        }
      }
    }
  }
  .for-dark {
    display: none;
  }
  &[class*="dark-"] {
    .horizontal-wrapper{
      .for-dark {
        display: block;
      }
      .for-light{
        display: none;
      }
    }
  }
}
