/**=====================
    33.  Ecommerce  CSS Start
==========================**/
.add-product{
  .dropzone{
    padding: 40px;
  }
  .product-info{
    h5{
      font-size: 16px;
      margin-bottom: 14px;
    }
    .dzu-dropzone{
      height: 240px;
      justify-content: center;
    }
    .product-group{
      border: 1px solid $light-gray;
      padding: 16px 18px;
      border-radius: 6px;
    }
  }
}
/**====== Product Start ======**/
.toggle-data {
  cursor: pointer;
}
.product-sidebar {
  .filter-sidebar{
    max-height: 1000px;
    overflow: auto;
  }
  .filter-section {
    .card {
      .card-header {
        padding: 16px 30px;
        border-radius: 5px;
        h6 {
          position: relative;
          .pull-right {
            i {
              position: absolute;
              top: 4px;
              left: 0;
              font-size: 10px;
              width: 100%;
              height: 10px;
              text-align: right;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
.product-wrapper {
  .product-sidebar {
    .filter-section {
      .card {
        .left-filter {
          z-index: 0;
          opacity: 0;
          visibility: hidden;
          height: 0;
          transition: 0.3s;
          .product-filter {
            .irs-with-grid {
              margin-bottom: 15px;
            }
          }
          .card-body {
            padding: 20px;
          }
        }
      }
    }
  }
  .product-grid {
    .product-wrapper-grid {
      margin-left: 0;
      transition: 0.3s;
    }
  }
  &.sidebaron {
    .product-sidebar {
      .filter-section {
        .card {
          .left-filter {
            z-index: 3;
            opacity: 1;
            visibility: visible;
            position: absolute;
            width: 100%;
            top: 53px;
            height: auto;
            border: 1px solid $light-gray;
            background-color: $white;
          }
        }
      }
    }
  }
}
.product-filter {
  .checkbox-animated {
    label {
      color: $light-text;
    }
  }
}
.product-table {
  overflow: hidden;
  .dataTable{
    thead{
      th{
        &.sorting_asc{
          text-align: center;
        }
        &:nth-child(n+3){
          text-align: center;
        }
      }
    }
    tbody{
      tr{
        td{
          &:nth-child(n+3){
            text-align: center;
          }
        }
      }
    }
  }
  img {
    height: 40px;
  }
  span,
  p {
    color: $semi-dark;
  }
  tbody {
    tr {
      td {
        &:first-child {
          text-align: center;
        }
      }
    }
  }
  .dataTables_wrapper {
    button {
      padding: 2px 12px;
      font-size: 14px;
    }
  }
}
.d-none-productlist {
  display: none;
  svg {
    vertical-align: middle;
    cursor: pointer;
  }
}
.product-wrapper-grid {
  &.list-view {
    .product-box {
      display: flex;
      align-items: center;
      .product-img {
        width: 30%;
      }
      .product-details {
        text-align: left;
      }
    }
    [class*="col-"] {
      width: 100%;
    }
  }
}
.slider-product {
  padding: 15px 0;
  border-top: 1px solid $light-gray;
  border-bottom: 1px solid $light-gray;
  margin-bottom: 15px;
}
.products-total {
  display: flex;
  align-items: center;
  > div {
    &:nth-child(n + 2) {
      margin-left: 10px;
    }
  }
}
.grid-options {
  height: 33px;
  margin-left: 10px;
  ul {
    li {
      a {
        position: relative;
        .line-grid {
          position: absolute;
          width: 4px;
          height: 15px;
          top: 9px;
        }
        .line-grid-1 {
          left: 12px;
        }
        .line-grid-2 {
          left: 18px;
        }
        .line-grid-3 {
          left: 36px;
        }
        .line-grid-4 {
          left: 42px;
        }
        .line-grid-5 {
          left: 48px;
        }
        .line-grid-6 {
          left: 66px;
        }
        .line-grid-7 {
          left: 72px;
        }
        .line-grid-8 {
          left: 78px;
        }
        .line-grid-9 {
          left: 84px;
        }
        .line-grid-10 {
          left: 103px;
        }
        .line-grid-11 {
          left: 109px;
        }
        .line-grid-12 {
          left: 115px;
        }
        .line-grid-13 {
          left: 121px;
        }
        .line-grid-14 {
          left: 127px;
        }
        .line-grid-15 {
          left: 133px;
        }
      }
    }
  }
}
.square-product-setting {
  height: 36px;
  vertical-align: middle;
  a {
    color: $theme-body-font-color;
  }
  .icon-grid {
    padding: 7px;
    background-color: rgba($primary-color, 0.05);
    display: flex;
    align-items: center;
    border-radius: 5px;
    svg {
      width: 20px;
      height: 20px;
      color: $primary-color;
    }
  }
}
.color-selector {
  line-height: 0.9;
  ul {
    li {
      display: inline-block;
      width: 25px;
      height: 25px;
      cursor: pointer;
      border: 1px solid $light-gray;
      border-radius: 6px;
      position: relative;
      &.active {
        &:before {
          content: "\f00c";
          position: absolute;
          top: 50%;
          left: 50%;
          margin-right: -50%;
          transform: translate(-50%, -50%);
          font-family: $font-awesome;
          color: $white;
        }
      }
      &.white {
        background-color: $white;
        &.active {
          &:before {
            color: $light-text;
          }
        }
      }
      &.orange {
        background-color: #ffb17a;
      }
      &.green {
        background-color: #6fb866;
      }
      &.pink {
        background-color: pink;
      }
      &.yellow {
        background-color: #f2f896;
      }
      &.blue {
        background-color: #63b4f2;
      }
      &.red {
        background-color: #ff647f;
      }
    }
  }
}
.product-filter {
  .banner-product {
    margin-top: 15px;
  }
  h6 {
    margin-bottom: 15px;
    font-size: 18px;
  }
  .checkbox-animated {
    label {
      font-weight: 500;
    }
  }
  &.new-products {
    margin-top: 20px;
    button {
      width: auto;
    }
    .owl-theme {
      .owl-nav {
        margin-top: 0;
        position: absolute;
        top: -46px;
        right: 0;
        button {
          &:focus {
            outline: $transparent-color;
          }
          span {
            font-size: 20px;
            &:focus {
              outline-color: $transparent-color;
            }
          }
        }
      }
      .owl-nav [class*="owl-"] {
        &:hover {
          background-color: $transparent-color;
          color: inherit;
        }
      }
      .owl-item {
        .item {
          .product-box {
            .d-flex {
              display: flex;
              align-items: center;
            }
            .product-img {
              width: 40%;
            }
            .product-details {
              padding: 25px 0;
              display: flex;
              align-items: center;
              ul {
                li {
                  display: inline-block;
                }
              }
            }
            + .product-box {
              margin-top: 15px;
            }
          }
        }
      }
    }
  }
}
.modal-content{
  .modal-header{
    .product-box.row{
      align-items: center;
    }
  }
}
.select2-drpdwn-product {
  .form-control {
    margin: 5px 0;
    background-color: rgba($primary-color, 0.05);
    color: $primary-color;
    font-weight: 500;
    padding: 7px 15px;
    border-radius: 5px;
  }
}
.feature-products {
  form {
    .form-group {
      position: relative;
      input {
        margin-bottom: 15px;
        height: 50px;
        padding-left: 30px;
      }
      i {
        position: absolute;
        top: 17px;
        right: 30px;
        color: $dark-gray;
      }
    }
  }
}
.product-box {
  background-color: $white;
  overflow: hidden;
  border-radius: 10px;
  .product-details {
    padding: 25px;
    h4 {
      font-weight: 600;
      color: $theme-body-font-color;
      font-size: 20px;
      line-height: 1;
      margin-bottom: 5px;
      text-transform: capitalize;
    }
    p {
      margin-bottom: 5px;
      color: $light-text;
      font-weight: 500;
      font-size: 13px;
    }
    h6 {
      color: $theme-body-font-color;
      text-transform: uppercase;
      margin-bottom: 10px;
    }
    span {
      color: $dark-gray;
    }
  }
  .modal {
    .modal-header {
      background-color: $white;
      .product-box {
        .product-details {
          padding: 25px 0;
          h6 {
            text-transform: capitalize;
          }
          .product-price {
            margin-bottom: 10px;
          }
          .product-view {
            padding: 20px 0;
            border-top: 1px solid rgba($primary-color, 0.2);
            border-bottom: 1px solid rgba($primary-color, 0.2);
          }
          .product-size {
            margin: 20px 0;
            ul {
              li {
                display: inline-block;
                background-color: rgba($primary-color, 0.1);
                border-radius: 5px;
                button {
                  color: $primary-color;
                  width: 35px;
                  height: 35px;
                  font-size: 16px;
                  padding: 0;
                  font-weight: 500;
                  border-radius: 5px;
                }
                &:nth-child(n + 2) {
                  margin-left: 15px;
                }
                &.active,
                &:hover {
                  button {
                    background-color: $primary-color !important;
                    color: $white;
                  }
                }
              }
            }
          }
          .product-qnty {
            fieldset {
              margin-bottom: 20px;
              .input-group {
                width: 35%;
                .btn {
                  padding: 5px 12px;
                }
              }
            }
            .addcart-btn {
              .btn {
                font-weight: 600;
              }
            }
          }
        }
      }
    }
  }
  .product-img {
    position: relative;
    .product-hover {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba($black, 0.2);
      opacity: 0;
      border-radius: 100%;
      transform: scale(0);
      transition: all 0.3s ease;
      ul {
        display: flex;
        align-items: center;
        i {
          font-size: 14px;
        }
        li {
          box-shadow: 1px 1px 2px 1px rgba(0, 0, 0, 0.08);
          background-color: $white;
          font-size: 18px;
          border-radius: 100%;
          line-height: 1.6;
          height: 45px;
          width: 45px;
          margin: 0 3px;
          display: flex;
          align-items: center;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          a {
            color: $primary-color;
          }
          .btn {
            padding: 0;
          }
          &:hover {
            background-color: $primary-color;
            .btn,
            i {
              color: $white;
            }
            transition: all 0.3s ease;
          }
        }
      }
    }
  }
  &:hover {
    .product-hover {
      opacity: 1;
      border-radius: 0%;
      transform: scale(1);
      transition: all 0.3s ease;
    }
  }
}
.pro-filter-sec {
  display: flex;
  justify-content: space-between;
  .product-sidebar {
    width: 375px;
    min-width: 375px;
    margin-right: 30px;
  }
  .product-search {
    width: 100%;
  }
}
.product-page-main {
  padding: 30px;
  .nav-tabs{
    border-bottom: none;
  }
  .owl-item {
    .item {
      border: 1px solid $light-gray;
      border-radius: 5px;
    }
    &.current {
      .item {
        border: 1px solid $primary-color;
      }
    }
  }
  .product-slider {
    margin-bottom: 20px;
  }
  .color-selector {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .pro-group {
    padding-top: 15px;
    padding-bottom: 15px;
    tr {
      line-height: 2;
    }
    &:nth-child(n + 2) {
      border-top: 1px solid $light-gray;
    }
    .btn {
      font-weight: 500;
    }
  }
  .pro-slide-right {
    margin-bottom: -20px;
    img {
      width: auto;
      height: 145px;
      margin: auto;
    }
    .slick-slide {
      margin-bottom: 20px;
    }
  }
  .border-tab {
    &.nav-tabs {
      .nav-item {
        .nav-link {
          border-radius: 30px;
          &.active {
            background-color: $primary-color;
            color: $white;
          }
        }
      }
    }
  }
}
.product-page-details {
  display: flex;
  justify-content: space-between;
  h4 {
    color: $theme-body-font-color;
    text-transform: uppercase;
    font-size: 18px;
  }
  span {
    padding-left: 15px;
  }
  h3 {
    font-size: 24px;
  }
}
.br-theme-fontawesome-stars {
  .br-widget {
    a,
    a.br-selected:after,
    a.br-active:after {
      color: #ffa800 !important;
      font: normal normal normal 14px/1 FontAwesome;
    }
  }
}
.payment-details{
  .card-body{
    .theme-form{
      input, textarea{
        &::placeholder{
          color: #898989 !important;
          font-weight: 400 !important;
        }
      }
    }
  }
}
.product-price {
  font-size: 16px;
  font-weight: 500;
  color: $primary-color;
  del {
    color: $light-font;
    margin-left: 15px;
    font-weight: 600;
    font-size: 14px;
  }
}
.color-selector {
  line-height: 1;
  li {
    display: inline-block;
    height: 20px;
    width: 20px;
  }
}
.product-page-main {
  p {
    font-size: 15px;
    color: $light-font;
  }
  .nav-link {
    text-transform: uppercase;
    font-weight: 500;
  }
  .pro-group {
    h6 {
      text-transform: capitalize;
    }
  }
}
.product-social {
  li {
    a {
      font-size: 15px;
      border: 1px solid $light-gray;
      border-radius: 100%;
      height: 35px;
      width: 35px;
      display: inline-block;
      text-align: center;
      line-height: 2.3;
      transition: all 0.3s ease;
      &:hover {
        background-color: $primary-color;
        i {
          color: $white;
        }
      }
    }
    &:nth-child(n + 2) {
      margin-left: 10px;
    }
  }
}
.filter-block {
  h4 {
    margin-bottom: 20px;
  }
  ul {
    li {
      color: $light-text;
      .form-check {
        margin-bottom: 0;
        label {
          margin-bottom: 0;
          vertical-align: middle;
        }
        .form-check-input {
          &:checked {
            background-color: $primary-color;
          }
        }
      }
      &:nth-child(n + 2) {
        margin-top: 15px;
      }
    }
  }
}
ul.pro-services {
  li {
    svg {
      color: $primary-color;
      margin-right: 15px;
    }
    h5 {
      font-size: 16px;
      margin-bottom: 5px;
    }
    p {
      font-size: 14px;
    }
    &:nth-child(n + 2) {
      margin-top: 20px;
    }
  }
}
.support-ticket {
  .pro-gress {
    .total-num {
      margin-bottom: 0;
    }
    .progress-showcase {
      margin-top: 30px;
    }
    span {
      font-weight: 500;
      color: $light-font;
    }
    ul {
      li {        
        span {
          i {
            margin-left: 10px;
          }
        }
      }
    }
  }
}
.support-ticket-font {
  h6 {
    font-weight: 500;
  }
}
.list-products {
  .product-table {
    table {
      th {
        min-width: 150px;
      }
    }
  }
}
/**====== Product  Ends ======**/
.payment-opt {
  li {
    display: inline-block;
    img {
      height: 20px;
      margin: 15px 15px 0 0;
    }
  }
}
@media (max-width: 1500px) {
  .product-page-main {
    .pro-slide-right {
      img {
        height: 130px;
      }
    }
  }
}
@media (max-width: 1470px) {
  .pro-filter-sec {
    .product-sidebar {
      width: 320px;
      min-width: 320px;
    }
  }
}
@media (max-width: 1366px) {
