          font-size: 12px;
        }
      }
    }
  }
}
@media (max-width: 1280px) and (min-width: 1200px) {
  .product-page-main {
    .xl-50 {
      max-width: 50% !important;
      flex: 0 0 50% !important;
    }
    .pro-slide-right {
      img {
        height: 110px;
      }
    }
  }
}
@media (max-width: 1280px) {
