                    font-size: 14px;
                  }
                  &:nth-child(n + 2) {
                    margin-left: 10px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .product-page-main {
    .singale-pro-slide {
      img {
        width: 50%;
        height: auto;
        margin-right: auto;
        margin-left: auto;
      }
    }
    .pro-group {
      .btn {
        padding: 7px 14px;
      }
    }
    .border-tab {
      &.nav-tabs {
        .nav-item {
          .nav-link {
            padding: 8px 18px;
            font-size: 14px;
          }
        }
      }
    }
  }
}
@media (max-width: 480px) {
  .add-product{
    .dropzone{
      min-height: 80px;
      padding: 18px;
    }
  }
  .product-page-main{
    p{
      font-size: 13px;
    }
  }
  .d-none-productlist{
    svg{
      height: 18px;
    }
  } 
  .learning-box {
    .details-main {
      margin: 15px;
      p {
        font-size: 13px;
      }
    }
  }
  .blog-box.blog-list {
    .blog-details {
      hr {
        margin-top: 5px;
        margin-bottom: 5px;
      }
    }
  }
  .product-page-main {
    .pro-group {
      .btn {
        padding: 7px 15px;
        font-size: 12px;
      }
    }
    .border-tab {
      &.nav-tabs {
        .nav-item {
          display: block;
          width: 100%;
        }
      }
    }
  }
  .product-page-details {
    display: block;
  }
  .square-product-setting {
    .icon-grid {
      display: none;
    }
  }
  .products-total {
    > div {
      &:nth-child(n + 2) {
        margin-left: unset;
        margin-right: unset;
      }
    }
  }
}
@media (max-width: 420px) {
  ul {
    &.pro-services {
      li {
        h5 {
